<template>
  <Modal
    v-model="isVisible"
    title="Shipping Kit Templates"
    size="lg"
    :hide-footer="!hasActionSlot"
    no-padding
    style="z-index: 1001"
  >
    <template #default>
      <div
        v-if="Array.isArray(templates) && templates?.length > 0 && !loading"
        class="px-12 py-10"
      >
        <label for="template" class="block mb-4">Select Template</label>
        <RadioGroup
          v-model="selectedTemplate"
          class="mb-4"
          id="template"
          name="template"
        >
          <RadioButton
            v-for="(template, templateIndex) of templates"
            :key="templateIndex"
            :label="`${templateIndex}`"
            >{{ template.name }}</RadioButton
          >
        </RadioGroup>
      </div>
      <Transition name="slide-fade">
        <VuePdf
          v-if="currentViewedPdfTemplate"
          :pdf="currentViewedPdfTemplate"
          :config="viewerConfig"
          theme="dark"
          style="height: 60vh"
        />
        <div
          v-else
          class="flex items-center justify-center bg-grey-lighten"
          style="height: 60vh"
        >
          <template v-if="loading">
            <Loading />
          </template>
          <template>
            <p>Unable to preview shipping kit template</p>
          </template>
        </div>
      </Transition>
    </template>
    <template v-if="hasActionSlot" #footer>
      <slot name="actions"></slot>
    </template>
  </Modal>
</template>
<script>
import { ref, computed, reactive, defineComponent, watch } from 'vue'

import VuePdf from 'vue3-pdf-app'
import Modal from 'bbUi/components/modal/modal.vue'
import Loading from 'components/BaseLoading.vue'

export default defineComponent({
  components: {
    VuePdf,
    Modal,
    Loading
  },
  props: {
    modelValue: {
      type: [Boolean, String, Number],
      default: false
    },
    templates: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'select-template'],
  setup(props, { emit, slots }) {
    const selectedTemplate = ref(0)
    const isVisible = computed({
      set: (value) => emit('update:modelValue', value),
      get: () => props.modelValue
    })
    const viewerConfig = reactive({
      toolbar: {
        toolbarViewerRight: false
      },
      secondaryToolbar: false
    })
    const hasActionSlot = computed(() => !!slots.actions)
    const currentViewedPdfTemplate = computed(() =>
      Array.isArray(props.templates) && props.templates?.length > 0
        ? props.templates[selectedTemplate.value]?.pdf || null
        : null
    )

    watch(
      () => ({
        selected: selectedTemplate.value,
        templates: props.templates
      }),
      ({ selected, templates }) => {
        if (Array.isArray(templates) && templates?.length > 0)
          emit('select-template', templates[selected])
      },
      {
        immediate: true
      }
    )
    return {
      selectedTemplate,
      isVisible,
      viewerConfig,
      hasActionSlot,
      currentViewedPdfTemplate
    }
  }
})
</script>
<style lang="scss">
.pdf-app.dark {
  --pdf-toolbar-color: #393939 !important;
  --pdf-app-background-color: #393939 !important;
  --pdf-overlay-container-dialog-color: #393939 !important;
  --pdf-sidebar-content-color: #393939 !important;
}
</style>
