<template>
	<div>	
		<Breadcrumb
			class="mt-2 mb-8"
			pageTitle="Manage Shipping"
			:backTo="{ name: 'setting' }"
			:items="breadcrumb">
			<template #right>
				<Button
					color="teal"
					:loading="loadingAllForm"
					@click="handleSaveAllChanges">
					Save Changes
				</Button>
			</template>
		</Breadcrumb>
		<Card class="mb-6">
			<template #header>
				<h5 class="text-lg font-medium">Set Default Label</h5>
			</template>
			<div v-loading="loadingAllForm" class="font-medium flex items-center mb-6">
				<Tooltip placement="right">
					<template #content>
						<div :style="{ maxWidth: '300px' }">
							When off, a shipping cost line appears under the tracking number on the Offer Details page, and no amount is deducted from the offer total. When on, the shipping cost is deducted from the offer. The cost is not final until USPS weighs the package, after which the actual shipping cost will be reflected. This feature is available only for EasyPost USPS services.
						</div>
					</template>
					<template #default>
						<Checkbox
							v-model="shippingLabel.shippingLabelCostEnabled.value"
							@update:model-value="handleUpdateShippingLabelCostEnabled"
							:value="true"
							:value-off="false"
							class="font-medium"
							color="teal"
							view="smooth"
							type="toggle"
							block />
					</template>
				</Tooltip>
				Add Shipping Cost to Offer Details page
			</div>
			<Fieldset class="rounded-lg">
				<template #label>Shipping Label</template>
				<div v-loading="loading.update.shipping_rules">
					<ShippingRulesForm
						ref="refShippingRulesForm"
						v-model:data="data.shipping_rules"
						:product-models="data.product_models"
						:hide="isHideShippingRules"
						@update-shipping-rules="updateShippingRules">
						<template v-if="isHideShippingRules" #message>
							<div class="py-6 flex items-center justify-center">
								<span class="text-center">
									Please set up at least one shipping provider first.
									<router-link :to="{ name: 'integration' }">Go to Add-ons</router-link>
								</span>
							</div>
						</template>
					</ShippingRulesForm>
				</div>
			</Fieldset>
		</Card>
		<Card class="mb-6">
			<template #header>
				<h5 class="text-lg font-medium">Advanced Shipping Rules</h5>
			</template>
			<template #default>
				<div
					v-if="!isHideShippingRules"
					v-loading="loadingAllForm">
					<Accordion
						v-model="shippingEnabledStatus"
						trigger="content"
						max-height="100%">
						<template #header>
							<div class="flex xl:w-96 lg:w-96 md:w-96 w-full">
								<Checkbox
									v-model="shippingEnabledStatus"
									@click.stop="handleUpdateShippingEnabledStatus"
									:value="true"
									:value-off="false"
									class="font-medium"
									color="teal"
									view="smooth"
									type="toggle"
									block
								/>
								<div class="flex items-center gap-x-1">
									<span class="my-4 font-medium">Advanced Shipping Rules</span>
									<Tooltip placement="top">
										<template #content>
											<div :style="{ maxWidth: '300px' }">
												Use this rule to assign different labels for specific models, conditions, and
												offer value
											</div>
										</template>
										<template #default>
											<i class="material-icons cursor-pointer text-teal-04">info</i>
										</template>
									</Tooltip>
								</div>
								<div>
									<div
										v-if="!isProPlan"
										class="block py-1 px-4 bg-green-04 text-white rounded-full mt-3 ml-3">
										<p>Pro</p>
									</div>
								</div>
							</div>
						</template>
						<template #default>
							<AdvanceShippingRules
								v-if="!isHideShippingRules && shippingEnabledStatus"
								:rules="shippingRules"
								:carriers="advanceShippingCarriers"
								@update:rules="handleUpdateAdvanceShippingRules"
							/>
						</template>
					</Accordion>
					<Accordion
						v-model="dataAdvanceShippingRuleMultipleLabel.accordion"
						trigger="content"
						max-height="100%">
						<template #header>
							<div class="flex xl:w-96 lg:w-96 md:w-96 w-full">
								<Checkbox
									v-model="dataAdvanceShippingRuleMultipleLabel.payload.is_enable_multiple_label"
									:value="1"
									:value-off="0"
									class="font-medium"
									color="teal"
									view="smooth"
									type="toggle"
									@change="handleUpdateShippingEnabledMultiple"
								/>
								<div class="flex items-center gap-x-1">
									<span class="my-4 font-medium">Multiple Shipping Rules</span>
									<Tooltip placement="top">
										<template #content>
											<div :style="{ maxWidth: '240px' }">
												{{ 'When customers submit a trade-in, they can request additional shipping labels. Set the per-offer limit up to 15 labels.' }}
											</div>
										</template>
										<template #default>
											<i class="material-icons cursor-pointer text-teal-04">info</i>
										</template>
									</Tooltip>
								</div>
								<div>
									<div
										v-if="!isProPlan"
										class="block py-1 px-4 bg-green-04 text-white rounded-full mt-3 ml-3">
										<p>Pro</p>
									</div>
								</div>
							</div>
						</template>
						<template #default>
							<AdvanceShippingRulesMutlipleLabels
								ref="advanceShippingRulesMutlipleLabels"
								v-model="dataAdvanceShippingRuleMultipleLabel.payload.rules"
								:enable="dataAdvanceShippingRuleMultipleLabel.payload.is_enable_multiple_label"
							/>
						</template>
					</Accordion>
				</div>
				<Accordion
					v-else
					v-loading="loadingAllForm"
					disableExpand>
					<template #header>
						<div class="py-6 flex items-center justify-center">
							<span class="text-center">
								Please set up at least one shipping provider first.
								<router-link :to="{ name: 'integration' }">Go to Add-ons</router-link>
							</span>
						</div>
					</template>
				</Accordion>
			</template>
		</Card>
		<ShippingKitTemplate
			v-loading="loadingAllForm"
			v-model:shipping-kit="stateShippingKit.current"
			v-model:modal="modalShippingKit"
			@update="handleSaveShippingKit"
			@update-default="handleUpdateDefaultShippingKit"
			@preview="handlePreviewShippingKit"
			@restore="handleRestoreShippingKit"
			@download="handleDownloadShippingKit"
			@showTemplates="handleShowTemplateShippingKit"
			:loading="loadingShippingKit"
			:preview-pdf="stateShippingKit.previewArrayBuffer"
			:templates="stateShippingKit.templates"
			:short-codes="stateShippingKit.short_codes"
			:use-legacy-template="isUseLegacyShippingTemplate"
		/>

		<Modal v-model="modalReset" hide-footer hide-header size="2xs" class="reset-shipping-rule">
			<div class="space-y-8 my-6">
				<img src="@/assets/icon/attention-orange.svg" alt="Info Icon" class="mx-auto w-24 h-24" />
				<div class="space-y-2 text-center">
					<p class="font-semibold">Disable Advanced Shipping Rules?</p>
					<p class="text-grey-03 text-sm">
						This will erase all of your progress and reset all of your settings. This action cannot
						be undone.
					</p>
				</div>
				<div class="flex items-center justify-center gap-x-4">
					<Button
						:loading="loadingAdvanceShippingRule.update"
						color="orange"
						view="outlined"
						@click="confirmResetShippingRule">
						Yes, Disable
					</Button>
					<Button
						:disabled="loadingAdvanceShippingRule.update"
						color="teal"
						@click="cancelResetShippingRule">
						Cancel
					</Button>
				</div>
			</div>
		</Modal>
	</div>
</template>

<script>
import { defineComponent, watch, ref, computed, nextTick } from 'vue'
import { useRoute } from 'vue-router'
import { useManageShipping } from 'composable/setting'
import {
	useShippingCarrier,
	useAdvanceShippingRules,
	useShippingLabel,
	useAdvanceShippingMultipleLabel
} from 'composable/shipping'
import { useShippingKit } from 'composable/setting/manageShipping'
import { useUser } from 'composable/user'
import { usePaywall } from 'composable/paywall'

import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Pagination from 'components/Pagination.vue'
import TextEditor from 'components/TextEditor.vue'
import { deepClone } from 'utils/deep-clone'
import ShippingRulesForm from 'components/ManageShipping/ShippingRulesForm.vue'
import AdvanceShippingRules from 'components/ManageShipping/AdvanceShippingRules.vue'
import ShippingAdditionalForm from 'components/ManageShipping/ShippingAdditionalForm.vue'
import ShippingKitForm from 'components/Integration/ShippingKitForm.vue'
import ShippingKitTemplate from 'components/ManageShipping/ShippingKitTemplate/index.vue'
import Fieldset from 'bbUi/components/fieldset'
import Accordion from 'components/Accordion.vue'
import AdvanceShippingRulesMutlipleLabels from 'components/ManageShipping/AdvanceShippingRulesMutlipleLabels.vue'

import { ElMessage } from 'element-plus'

export default defineComponent({
	components: {
		Button,
		Card,
		Input,
		Select,
		Modal,
		Fieldset,
		Pagination,
		TextEditor,
		Breadcrumb,
		Checkbox,
		ShippingRulesForm,
		AdvanceShippingRules,
		ShippingKitForm,
		ShippingAdditionalForm,
		ShippingKitTemplate,
		Accordion,
		AdvanceShippingRulesMutlipleLabels
	},
	async setup(props, { emit }) {
		const {
			state,
			loading,
			actionAll,
			data,
			actionUpdateShippingRules,
			actionUpdateEnableAdvancedShippingRules,
			actionUpdateShippingErrorLabel,
			actionUpdateAdditionalSetting
		} = useManageShipping()
		const { shippingCarriers } = useShippingCarrier()
		const {
			loading: loadingAdvanceShippingRule,
			shippingRules,
			shippingCarriers: advanceShippingCarriers,
			setShippingRules,
			unsetShippingRules,
			actionUpdate: updateAdvanceShippingRules,
			shippingEnabledStatus,
		} = useAdvanceShippingRules()
		const {
			loading: loadingAdvanceShippingRuleMultipleLabel,
			data: dataAdvanceShippingRuleMultipleLabel,
			initData: initDataAdvanceShippingRuleMultipleLabel,
			validation: validationAdvanceShippingRuleMultipleLabel
		} = useAdvanceShippingMultipleLabel()
		const {
			state: stateShippingKit,
			fetch: fetchShippingKit,
			preview: previewShippingKit,
			update: updateShippingKit,
			loading: loadingShippingKit,
			isUseLegacyShippingTemplate,
			modal: modalShippingKit,
			restore: restoreShippingKit,
			download: downloadShippingKit,
			updateDefaultTemplate: updateDefaultShippingKitTemplate,
			previewArrayBuffer: shippingKitPreviewUrl,
			showTemplates
		} = useShippingKit()
		const shippingLabel = useShippingLabel()
		const { open: openPaywall } = usePaywall()
		const { isProPlan } = useUser()
		const route = useRoute()
		const refShippingRulesForm = ref(null)
		const refAdvancedShippingRulesForm = ref(null)
		const refShippingErrorLabelForm = ref(null)
		const refShippingAdditionalForm = ref(null)
		const isSavedShippingRules = ref(false)
    const loadingAllForm = ref(false)
		const breadcrumb = [
			{
				text: 'Settings',
				to: {
					name: 'setting'
				}
			},
			{
				text: 'Manage Shipping'
			}
		]
		const modalReset = ref(false)
		const advanceShippingRulesMutlipleLabels = ref()

		watch(
			() => state.value,
			state => {
				Object.assign(data, deepClone(state))
			},
			{
				immediate: true,
				deep: true
			}
		)
		const updateShippingRules = () => {
			const isValid = refShippingRulesForm.value.validate()
			if (isValid) {
				actionUpdateShippingRules(() => {
					ElMessage({
						type: 'success',
						message: 'Shipping rules updated!'
					})
				})
			}
		}
		const updateEnableAdvancedShippingRules = payload => {
			actionUpdateEnableAdvancedShippingRules(payload, () => {
				ElMessage({
					type: 'success',
					message: 'Shipping rules updated!'
				})
			})
		}
		const updateShippingErrorLabel = () => {
			const isValid = refShippingErrorLabelForm.value.validate()
			if (isValid) {
				actionUpdateShippingErrorLabel(() => {
					ElMessage({
						type: 'success',
						message: 'Shipping error label updated!'
					})
				})
			}
		}
		const updateAdditionalSetting = async () => {
			const isValid = refShippingAdditionalForm.value.validate()
			if (isValid) {
				await actionUpdateAdditionalSetting()
				ElMessage({
					type: 'success',
					message: 'Shipping additional settings updated'
				})
			}
		}
		const handleUpdateAdvanceShippingRules = (rules = null) => {
			if (!rules && !Array.isArray(rules)) return
			setShippingRules(rules)
		}
		const handleSaveAdvanceShippingRules = async () => {
			isSavedShippingRules.value = true
			if (!isProPlan.value) {
				openPaywall({
					title: 'Advanced Shipping Rules',
					description:
						'Set shipping rules based on item condition, item category, or price. Route specific conditions or item categories to different warehouses or shipping providers. Assign different shipping services based on value of package.',
					image_url: '/images/paywall/paywall-advanced-shipping-rules.svg'
				})
				return
			}
			await updateAdvanceShippingRules(({
				is_enable_multiple_label: dataAdvanceShippingRuleMultipleLabel.payload.is_enable_multiple_label,
				rules: dataAdvanceShippingRuleMultipleLabel.payload.rules,
			}), err => {
				if (!err)
					ElMessage({
						type: 'success',
						message: 'Shipping rules updated'
					})
			})
		}
		const handlePreviewShippingKit = async () => {
			await previewShippingKit(
				{
					textinfo: stateShippingKit.current.textinfo
				},
				err => {
					if (err) return
					modalShippingKit.preview = true
				}
			)
		}
		const handleSaveShippingKit = async () => {
			await updateShippingKit(
				{
					textinfo: stateShippingKit.current.textinfo,
					key: stateShippingKit.current.key
				},
				err => {
					if (err) return
					ElMessage({
						type: 'success',
						message: 'Shipping kit template updated'
					})
				}
			)
		}
		const handleRestoreShippingKit = async () => {
			await restoreShippingKit(
				{
					key: stateShippingKit.current.key
				},
				err => {
					if (err) return
					ElMessage({
						type: 'success',
						message: 'Shipping kit template restored'
					})
				}
			)
		}
		const handleDownloadShippingKit = () => {
			downloadShippingKit()
		}
		const handleUpdateDefaultShippingKit = async (selectedTemplate = undefined) => {
			if (!selectedTemplate) return
			await updateDefaultShippingKitTemplate(
				{
					default_textinfo_shippingkit: selectedTemplate.key
				},
				async err => {
					if (err) return
					await fetchShippingKit({ refetch: true })
					modalShippingKit.template = false
					ElMessage({
						type: 'success',
						message: 'Shipping kit template updated'
					})
				}
			)
		}
		const confirmResetShippingRule = async () => {
			shippingEnabledStatus.value = false
			handleSaveAdvanceShippingRules().then(() => {
				unsetShippingRules()
				isSavedShippingRules.value = false
				modalReset.value = false
			})
		}
		const cancelResetShippingRule = () => {
			shippingEnabledStatus.value = true
			modalReset.value = false
		}
		const handleUpdateShippingEnabledStatus = () => {
			if (shippingEnabledStatus.value && isSavedShippingRules.value) {
				modalReset.value = true
				return
			}
			if (!isProPlan.value) {
				openPaywall({
					title: 'Advanced Shipping Rules',
					description:
						'Set shipping rules based on item condition, product category, or price. Route specific conditions or product categories to different warehouses or shipping providers. Assign different shipping services based on value of package.',
					image_url: '/images/paywall/paywall-advanced-shipping-rules.svg'
				})
				shippingEnabledStatus.value = false
				return
			}
			shippingEnabledStatus.value = !shippingEnabledStatus.value
		}
		const handleUpdateShippingLabelCostEnabled = () => {
			updateAdvanceShippingRules()
		}
    const handleSaveAllChanges = async () => {
			const isValidMultipleShipping = await advanceShippingRulesMutlipleLabels.value.handleSubmit()
			if (!isValidMultipleShipping) {
				await nextTick()
				return advanceShippingRulesMutlipleLabels.value?.formMultiple?.scrollIntoView({
					behavior: 'smooth',
					block: 'end',
					inline: 'nearest'
				})
			}

      loadingAllForm.value = true
      await handleSaveAdvanceShippingRules()
      await handleSaveShippingKit()
      loadingAllForm.value = false
    }
		const handleShowTemplateShippingKit = async () => {
			await showTemplates()
			modalShippingKit.template_list = true
		}
		const handleUpdateShippingEnabledMultiple = () => {
			// if (shippingEnabledMultiple.value && isSavedShippingRules.value) {
			// 	modalReset.value = true
			// 	return
			// }
			// shippingEnabledMultiple.value = !shippingEnabledMultiple.value
			if (
				!isProPlan.value &&
				dataAdvanceShippingRuleMultipleLabel.payload.is_enable_multiple_label === 1
			) {
				openPaywall({
					title: 'Multiple Shipping Label Rules',
					description:
						'Unlock the ability to set custom rules for generating multiple shipping labels per offer. Streamline bulk shipments, save time, and give your customers the flexibility to request the exact number of labels they need for each trade-in. Enable advanced control and efficiency.',
					image_url: '/images/paywall/paywall-multiple-shipping-labels.svg'
				})
				dataAdvanceShippingRuleMultipleLabel.payload.is_enable_multiple_label = 0
				return
			}
			if (dataAdvanceShippingRuleMultipleLabel.payload.is_enable_multiple_label === 1) {
				dataAdvanceShippingRuleMultipleLabel.accordion = true
			} else {
				dataAdvanceShippingRuleMultipleLabel.accordion = false
			}
		}

		const isHideShippingRules = computed(() => shippingCarriers.value.length < 1)
		await actionAll(true, (res) => {
			initDataAdvanceShippingRuleMultipleLabel(res)
		})
		await fetchShippingKit()

		if (shippingEnabledStatus.value) isSavedShippingRules.value = true

		return {
			state,
			stateShippingKit,
			loading,
      loadingAllForm,
			loadingAdvanceShippingRule,
			loadingShippingKit,
			shippingKitPreviewUrl,
			modalShippingKit,
			data,
			breadcrumb,
			shippingEnabledStatus,
			isHideShippingRules,
			isUseLegacyShippingTemplate,
			isProPlan,
			refShippingRulesForm,
			refAdvancedShippingRulesForm,
			refShippingErrorLabelForm,
			refShippingAdditionalForm,
			isSavedShippingRules,
			shippingRules,
			advanceShippingCarriers,
			modalReset,
			shippingLabel,
			loadingAdvanceShippingRuleMultipleLabel,
			dataAdvanceShippingRuleMultipleLabel,
			validationAdvanceShippingRuleMultipleLabel,
			advanceShippingRulesMutlipleLabels,
			updateShippingRules,
			updateShippingErrorLabel,
			updateEnableAdvancedShippingRules,
			updateAdditionalSetting,
			handleUpdateAdvanceShippingRules,
			handleSaveAdvanceShippingRules,
			handlePreviewShippingKit,
			handleSaveShippingKit,
			handleRestoreShippingKit,
			handleDownloadShippingKit,
			handleUpdateDefaultShippingKit,
			confirmResetShippingRule,
			cancelResetShippingRule,
			handleUpdateShippingEnabledStatus,
			handleUpdateShippingLabelCostEnabled,
      handleSaveAllChanges,
			handleShowTemplateShippingKit,
			handleUpdateShippingEnabledMultiple
		}
	}
})
</script>