<template>
  <Table class="mt-6" v-if="Array.isArray(shortCodes)" :data="shortCodes">
    <TableColumn label="Code">
      <template #default="scope">
        <div
          :class="[
            'short-code',
            scope.row.is_disabled ? 'short-code--disabled' : ''
          ]"
        >
          <span class="mr-2">{{ scope.row.code }}</span>
          <Tooltip placement="top" :disabled="!scope.row.is_disabled">
            <template #content>
              <div class="w-96">
                <span>{{
                  scope.row.tooltip
                    ? scope.row.tooltip
                    : 'No information provided'
                }}</span>
              </div>
            </template>
            <template #default>
              <i
                v-if="!scope.row.is_disabled"
                class="material-symbols-outlined text-lg text-lg text-teal-04"
                @click="copyText(scope.row.code)"
              >
                content_copy
              </i>
              <i v-else class="material-icons text-lg text-teal-04">info</i>
            </template>
          </Tooltip>
        </div>
      </template>
    </TableColumn>
    <TableColumn label="Description">
      <template #default="scope">
        <div
          :class="[
            'inline-block',
            'short-code',
            scope.row.is_disabled ? 'short-code--disabled' : ''
          ]"
        >
          {{ scope.row.description }}
        </div>
      </template>
    </TableColumn>
  </Table>
</template>
<script>
import { defineComponent, computed } from 'vue'
import copyText from 'utils/copy'
export default defineComponent({
  props: {
    shortCodes: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const shortCodes = computed(() => props.shortCodes)
    return {
      shortCodes,
      copyText
    }
  }
})
</script>
<style lang="scss" scoped>
.short-code {
  @apply flex;
  &--disabled {
    @apply text-grey-03;
  }
}
</style>
