<template>
  <div class="w-full">
    <h6 class="font-semibold text-base text-grey-04 ml-2 mb-1">{{label}}</h6>
    <div class="flex flex-wrap">
      <div v-for="(item, itemIndex) of items" :key="itemIndex" :class="['selectable-card', isSelected(item) && '--selected' || '']" @click="selectItem(item)">
        <slot name="default" :item="item"></slot>
      </div>
    </div>
  </div>
</template>
<script>
import { computed, defineComponent, ref, watch } from 'vue'
import { isFunction } from 'utils/is'

export default defineComponent({
  props: {
    modelValue: {
      type: [String, Object],
      default: () => ''
    },
    items: {
      type: Array,
      default: () => ([])
    },
    by: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: ""
    },
    multiple: {
      type: Boolean,
      default: false
    },
    filter: {
      type: Function
    }
  },
  emits: ['update:modelValue'],
  setup(props, {emit}){
    const selectedItems = ref([])
    const items = ref(props.items)
    const isObject = (data) => !!data && typeof data === 'object' && !Array.isArray(data)

    const selectItem = (item) => {
      const isString = typeof item === 'string'
      if(!props.multiple){
        if(isObject(item)){
          if(!props.by) return console.warn(`Object data needs to provide 'by' prop`)
          if(props.by) selectedItems.value[0] = item[props.by]
          return
        }
        if(isString){
          selectedItems.value[0] = item
          return
        }
        return console.warn(`Data needs to be either object or string, ${typeof item} given`)
      }
    }
    const isSelected = (item) => isObject(item) && props.by ? selectedItems.value.includes(item[props.by]) && '--selected' || '' : selectedItems.value.includes(item) && '--selected' || ''
    const resetSelected = () => {
      selectedItems.value = []
    }
    watch(selectedItems.value, (selected) => {
      if(!props.multiple) return emit('update:modelValue', selected[0])
    })

    watch(() => props.items, () => {
      if(props.items){
        items.value = props.items
      }
      if(props.filter){
        items.value = isFunction(props.filter) && items.value.filter(props.filter) || items.value
      }
      if(!props.multiple) selectedItems.value[0] = isObject(props.modelValue) && props.by ? props.modelValue[props.by] : props.modelValue
    }, {
      immediate: true
    })
    return {
      selectedItems,
      items,
      resetSelected,
      selectItem,
      isObject,
      isSelected
    }
  }
})
</script>
