<template>
  <div class="a-shipping-rule">
    <Draggable :model-value="rules" @update:model-value="handleArrangeRules" handle=".a-shipping-rule-action__handle">
      <div
        v-for="(rule, ruleIndex) of rules"
        :key="ruleIndex"
        class="a-shipping-rule__carrier"
      >
        <div class="a-shipping-rule-action">
          <i class="material-icons a-shipping-rule-action__handle"
            >drag_handle</i
          >
          <Checkbox
            class="a-shipping-rule-action__status"
            :model-value="rule.is_enable"
            @update:model-value="handleToggleRuleStatus(rule)"
            :value="1"
            :value-off="0"
          />
        </div>
        <div class="a-shipping-rule-rules">
          <span class="a-shipping-rule-rules__label">
            {{ rule.statement }}
          </span>
          <div class="a-shipping-rule-rules__rule-group">
            <template v-if="rule.name === 'condition' || rule.name === 'model'">
              <Select max-height="300px" :disabled="!rule.is_enable" block>
                <template #selected>
                  <template
                    v-if="
                      Array.isArray(rule.selected_option) &&
                      rule.selected_option.length > 0
                    "
                  >
                    <div class="flex items-center flex-wrap">
                      <div
                        v-for="(
                          selectedOption, selectedOptionIndex
                        ) of rule.selected_option.slice(0, 5)"
                        :key="selectedOptionIndex"
                        :class="[
                          'option',
                          !rule.is_enable ? 'option--disabled' : ''
                        ]"
                      >
                        {{ selectedOption }}
                      </div>

                      <span v-if="rule.selected_option.length > 4" class="mx-2">
                        +{{ rule.selected_option.length - 4 }} more</span
                      >
                    </div>
                  </template>
                  <template v-else>
                    <span>{{ rule.placeholder }}</span>
                  </template>
                </template>
                <template #sheet>
                  <div v-if="rule.name === 'model'" class="p-4">
                    <Input
                      v-model="searchModelQuery"
                      placeholder="Search Item"
                      block
                      icon
                    >
                      <template #icon>
                        <i class="material-icons">search</i>
                      </template>
                    </Input>
                  </div>
                  <div class="p-4">
                    <div class="py-2">
                      <Checkbox
                        :model-value="rule.selected_option.length > 0"
                        :indeterminate="
                          rule.selected_option.length !== 0 &&
                          rule.selected_option.length !== rule.options.length
                        "
                        @click.prevent="handleSelectAllOption(rule)"
                      >
                        <span class="font-normal">Select All</span>
                      </Checkbox>
                    </div>
                    <hr class="border border-grey-01" />
                    <div
                      v-for="(option, optionIndex) of rule.name === 'model'
                        ? filteredModels
                        : rule.options"
                      :key="optionIndex"
                      class="py-2"
                    >
                      <Checkbox
                        :model-value="rule.selected_option"
                        @update:model-value="
                          (value) => handleUpdateRuleOption(rule, value)
                        "
                        :value="option.value"
                      >
                        <span class="font-normal">{{ option.name }}</span>
                      </Checkbox>
                    </div>
                  </div>
                </template>
              </Select>
            </template>
            <template v-else>
              <Select
                :model-value="rule.selected_option || ''"
                @update:model-value="
                  (option) => handleUpdateRuleOption(rule, option)
                "
                :items="rule.name === 'model' ? filteredModels : rule.options"
                by="value"
                name="name"
                :placeholder="rule.placeholder"
                :disabled="!rule.is_enable"
                max-height="300px"
                block
              ></Select>
              <Input
                v-if="rule.name === 'order'"
                v-maska="'#*.##'"
                :model-value="rule.over_value"
                @update:model-value="
                  (value) => handleUpdateRuleOverValue(rule, value)
                "
                :rtl="true"
                class="mx-4"
                :disabled="!rule.is_enable"
                icon
                icon-block
              >
                <template #icon>
                  <Currency only-symbol class="text-xl" />
                </template>
              </Input>
            </template>
          </div>
        </div>
        <div class="a-shipping-rule-shipping">
          <span class="a-shipping-rule-shipping__label">Then Use</span>
          <div class="a-shipping-rule-shipping__carrier">
            <Select
              max-height="200px"
              :model-value="rule.selected_carrier"
              @update:model-value="
                (option) => handleUpdateRuleCarrier(rule, option)
              "
              :items="carriers"
              name="name"
              by="value"
              group-by="carriers"
              block
              placeholder="Select Carrier"
              :disabled="!rule.is_enable"
            >
              <template #item="{ item }">
                <div class="flex">
                  <div class="mr-4">
                    <img
                      class="w-8"
                      :src="item.icon"
                      :alt="`${item.name} icon`"
                    />
                  </div>
                  <div class="flex-auto" style="word-break: normal">
                    <span>{{ item.name }}</span>
                  </div>
                </div>
              </template>
            </Select>
          </div>
        </div>
      </div>
    </Draggable>
    <slot></slot>
  </div>
</template>
<script>
import { computed, defineComponent, ref, watch } from 'vue'

import { VueDraggableNext } from 'vue-draggable-next'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'components/Select.vue'
import TreeCheckbox from 'components/TreeCheckbox.vue'
import Currency from 'components/Currency.vue'

import { deepClone } from 'utils/deep-clone'

export default defineComponent({
  name: 'AdvanceShippingRules',
  components: {
    Draggable: VueDraggableNext,
    Checkbox,
    Input,
    Select,
    TreeCheckbox,
    Currency
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    rules: {
      type: Array,
      default: () => []
    },
    carriers: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:rules'],
  setup(props, { emit }) {
    const rules = ref([])
    const carriers = ref([])
    const searchModelQuery = ref('')

    const filteredModels = computed(() => {
      const modelRule = rules.value.find((rule) => rule.name === 'model')
      if (!modelRule) return []
      return searchModelQuery.value
        ? modelRule.options.filter(
            (option) =>
              option.value
                .toLowerCase()
                .indexOf(searchModelQuery.value.toLocaleLowerCase()) > -1
          )
        : modelRule.options
    })

    const handleToggleRuleStatus = (rule = null) => {
      if (!rule) return
      rule.is_enable = !rule.is_enable
      emit('update:rules', rules.value)
    }
    const handleUpdateRuleOption = (rule, option) => {
      if (!rule) return
      rule.selected_option = option
      emit('update:rules', rules.value)
    }
    const handleUpdateRuleCarrier = (rule, carrier) => {
      if (!rule) return
      rule.selected_carrier = carrier
      emit('update:rules', rules.value)
    }
    const handleUpdateRuleOverValue = (rule, value) => {
      if (!rule) return
      rule.over_value = value
      emit('update:rules', rules.value)
    }
    const handleSelectAllOption = (rule) => {
      if (!rule) return
      if (!Array.isArray(rule.selected_option)) return

      const isCheckedAll = rule.selected_option.length === rule.options.length

      if (isCheckedAll) {
        rule.selected_option = []
      } else {
        rule.selected_option = rule.options.map((rule) => rule.value)
      }

      emit('update:rules', rules.value)
    }
    const handleArrangeRules = (orderedRules) => {
      rules.value = orderedRules
      emit('update:rules', rules.value)
    }

    watch(
      () => props.rules,
      () => {
        try {
          rules.value = deepClone(props.rules)
        } catch (err) {
          rules.value = []
        }
      },
      {
        immediate: true
      }
    )

    watch(
      () => props.carriers,
      () => {
        carriers.value = props.carriers.map((carrier) => ({ ...carrier }))
      },
      {
        immediate: true,
        deep: true
      }
    )

    return {
      rules,
      carriers,
      searchModelQuery,
      filteredModels,
      handleToggleRuleStatus,
      handleUpdateRuleOption,
      handleUpdateRuleCarrier,
      handleUpdateRuleOverValue,
      handleSelectAllOption,
      handleArrangeRules
    }
  }
})
</script>
<style lang="scss">
.a-shipping-rule {
  &__carrier {
    @apply p-4 box-border bg-grey-lighten rounded-lg border border-grey-01 my-4 flex items-center md:flex-nowrap flex-wrap;
  }
}
.a-shipping-rule-action {
  @apply flex items-center;
  &__handle {
    @apply mx-4 cursor-move select-none p-2;
  }
  &__status {
    @apply mx-4;
  }
}

.a-shipping-rule-rules {
  @apply flex items-center w-full md:flex-nowrap flex-wrap;
  &__label {
    @apply mx-4 my-4 font-semibold;
  }
  &__rule {
    @apply mx-4 my-4;
  }
  &__rule-group {
    @apply flex-auto my-4 inline-flex items-center;
  }
}

.a-shipping-rule-shipping {
  @apply flex items-center w-full;
  &__label {
    @apply mx-4 font-semibold;
  }
  &__carrier {
    @apply flex-auto;
  }
}

.option {
  @apply px-4 py-1 bg-teal-01 text-teal-04 rounded-full m-1;
  &.option--disabled {
    @apply bg-grey-02 text-grey-04 rounded-full m-1;
  }
}
</style>
