import { reactive, computed } from 'vue'
import store from 'store'
import { isFunction } from 'utils/is'
import { anchorDownload } from 'utils/element'

const state = reactive({
  shipping_kit: {
    is_use_new_shipping_template: true,
    templates: [],
    current: {
      id: null,
      key: null,
      name: null,
      textinfo: null
    },
    loading: {
      fetch: false,
      update: false,
      preview: false,
      restore: false,
      template_list: false,
      update_default_template: false
    },
    modal: {
      preview: false,
      restore: false,
      template: false,
      template_list: false
    },
    previewArrayBuffer: null,
    short_codes: []
  }
})

const useShippingKit = () => {
  const templates = computed(() => state.shipping_kit.templates)
  const loading = computed(() => state.shipping_kit.loading)
  const isUseLegacyShippingTemplate = computed(() => state.shipping_kit.is_use_new_shipping_template === false)
  const previewArrayBuffer = computed(
    () => state.shipping_kit.previewArrayBuffer
  )

  const setShippingTemplates = async (templates = []) => {
    try {
      if (!templates) return
      state.shipping_kit.loading.template_list = true

      state.shipping_kit.templates = []
      const fetchedPdfs = []

      for (let [templateIndex, template] of Object.entries(templates)) {
        state.shipping_kit.templates.push({
          ...template,
          pdf: null
        })
        fetchedPdfs.push(
          fetchTemplatePdf({ key: template.key }, (err, pdf) => {
            if (err) return
            state.shipping_kit.templates[templateIndex].pdf = pdf
          })
        )
        await Promise.all(fetchedPdfs)
      }
    } catch (err) {
      console.warn(err)
    } finally {
      state.shipping_kit.loading.template_list = false
    }
  }
  const setCurrentShippingKit = (currentShippingKit = {}) => {
    if (!currentShippingKit) return
    state.shipping_kit.current = currentShippingKit
  }
  const setShortCodes = (shortCodes = []) => {
    if (!Array.isArray(shortCodes)) return
    state.shipping_kit.short_codes = shortCodes
  }
  const fetch = async (callback = () => {}) => {
    try {
      state.shipping_kit.loading.fetch = true
      const response = await store.dispatch(
        'setting/manageShipping/fetchShippingKit'
      )
      setShippingTemplates(response.list_template)
      setCurrentShippingKit(response.data)
      setShortCodes(response.data.shortcode)
      state.shipping_kit.is_use_new_shipping_template = response.use_new_template_shippingkit == 1
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.shipping_kit.loading.fetch = false
    }
  }
  const fetchTemplatePdf = async (
    payload = { key: null },
    callback = () => {}
  ) => {
    try {
      const response = await store.dispatch(
        'setting/manageShipping/fetchShippingKitTemplatePdf',
        payload.key
      )
      isFunction(callback) && callback(null, response)
    } catch (err) {
      isFunction(callback) && callback(err, null)
    }
  }
  const preview = async (payload = { textinfo: null }, callback = () => {}) => {
    try {
      state.shipping_kit.loading.preview = true
      const response = await store.dispatch(
        'setting/manageShipping/previewShippingKit',
        payload
      )
      state.shipping_kit.previewArrayBuffer = response
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.shipping_kit.loading.preview = false
    }
  }
  const update = async (
    payload = { textinfo: null, key: null },
    callback = () => {}
  ) => {
    try {
      state.shipping_kit.loading.update = true
      await store.dispatch('setting/manageShipping/updateShippingKit', payload)
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.shipping_kit.loading.update = false
    }
  }
  const restore = async (payload = { key: null }, callback = () => {}) => {
    try {
      state.shipping_kit.loading.restore = true
      const response = await store.dispatch(
        'setting/manageShipping/restoreShippingKit',
        payload
      )
      const { key, textinfo } = response
      state.shipping_kit.current.key = key
      state.shipping_kit.current.textinfo = textinfo
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.shipping_kit.loading.restore = false
      state.shipping_kit.modal.restore = false
    }
  }
  const download = (callback = () => {}) => {
    try {
      isFunction(callback) && callback(null)
      anchorDownload(state.shipping_kit.previewArrayBuffer, 'shipping-kit')
    } catch (err) {
      isFunction(callback) && callback(err)
    }
  }
  const updateDefaultTemplate = async (
    payload = { default_textinfo_shippingkit: null },
    callback = () => {}
  ) => {
    try {
      state.shipping_kit.loading.update_default_template = true
      await store.dispatch(`setting/update`, payload)
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.shipping_kit.loading.update_default_template = false
    }
  }
  return {
    state: state.shipping_kit,
    modal: state.shipping_kit.modal,
    templates,
    loading,
    isUseLegacyShippingTemplate,
    previewArrayBuffer,
    fetch,
    preview,
    update,
    restore,
    download,
    updateDefaultTemplate
  }
}

export { useShippingKit }
