<template>
  <div class="space-y-8">
    <div ref="formMultiple" class="space-y-4">
      <div
        v-for="(rule, n) in rules"
        :key="n">
        <div class="flex gap-4">
          <div>
            <Input v-model="rule.offer_type" readonly disabled />
          </div>
          <div class="w-96">
            <Input
              v-model="rule.label_qty"
              :view="validation?.rules[n]?.label_qty.$invalid ? 'danger' : ''"
              block
              @input="handleInputQty($event, n)"
            />
            <div v-if="validation?.rules[n]?.label_qty?.$errors?.length">
              <small
                v-for="error in validation?.rules[n]?.label_qty?.$errors"
                :key="error.$uid"
                class="text-red-04">
                {{ error?.$message }}
              </small>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, watch, computed } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { minValue, maxValue, required, helpers } from '@vuelidate/validators'

import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

export default defineComponent({
  name: 'AdvanceShippingRulesMutlipleLabels',
  components: {
    Input,
    Checkbox
  },
  props: {
    modelValue: {
      type: Array,
      default: () => []
    },
    enable: {
      type: Number,
      default: 0
    }
  },
  emits: ['update:modelValue'],
  setup(props, ctx) {
    const formMultiple = ref()
    const rules = ref([
      { offer_type: 'Mail-In', label_qty: 1 },
      { offer_type: 'Bulk Offer', label_qty: 1 },
      { offer_type: 'Easy Offer', label_qty: 1 }
    ])
    const rulesValidation = computed(() => {
      const result = {}
      rules.value.forEach((_, n) => {
        result[n] = {
          label_qty: {
            required: helpers.withMessage('Quantity is required', required),
            minValue: helpers.withMessage('Minimum quantity must be 1', minValue(1)),
            maxValue: helpers.withMessage('Maximum quantity allowed is 15', maxValue(15))
          }
        }
      })

      return { rules: result }
    })
    const validation = useVuelidate(rulesValidation, { rules }, {
      $lazy: true
    })
    const handleSubmit = async () => {
      const isValid = await validation.value.$validate()
      if (!isValid) return

      const result = rules.value
      ctx.emit('update:modelValue', result.map(rule => {
        return ({
          offer_type: rule.offer_type,
          label_qty: rule?.label_qty ?? null
        })
      }))
      return true
    }
    const handleInputQty = async (e, n) => {
      await validation.value?.rules[n]?.label_qty?.$validate()
    }

    watch(() => ({
      ruleVal: props.modelValue,
      enableVal: props.enable
    }), (value, oldValue) => {
      if (value.enableVal) {
        if (JSON.stringify(value?.ruleVal ?? '') !== JSON.stringify(oldValue?.ruleVal ?? '')) {
          rules.value = rules.value.map(rule => {
            const find = [...value.ruleVal].find(ruleOrigin =>
              Array.isArray(ruleOrigin.offer_type)
                ? ruleOrigin.offer_type.includes(rule.offer_type)
                : rule.offer_type === ruleOrigin.offer_type
            )
            if (find) rule.label_qty = find.label_qty

            return rule
          })
        }
      }
    }, {
      deep: true,
      immediate: true
    })

    return {
      rules,
      validation,
      formMultiple,
      handleSubmit,
      handleInputQty
    }
  }
})
</script>

<style lang="scss" scoped>
.button-remove__rule {
  @apply text-red-04 border border-red-04 bg-red-04 bg-opacity-0 border-opacity-0 flex items-center justify-center h-8 w-8 rounded-lg;

  &:hover {
    @apply border-opacity-100 bg-opacity-20;
  }
}
</style>
