<template>
  <TextEditor
    :modelValue="value"
    @update:modelValue="(value) => $emit('update:modelValue', value)"
    :options="textEditorOptions"
    with-image-upload
    :toolbar-items="toolbarItems"
  >
    <template #toolbar>
      <slot name="toolbar"></slot>
    </template>
  </TextEditor>
</template>
<script>
import { computed, reactive } from 'vue'

import { Editor } from '@erdeprsty/ckeditor5-custom-build'
import TextEditor from 'components/TextEditor.vue'

export default {
  components: {
    TextEditor
  },
  props: {
    modelValue: {
      type: String,
      default: ''
    }
  },
  emits: ['update:modelValue', ''],
  setup(props, { emit }) {
    const textEditorOptions = reactive({
      editor: Editor,
      config: {
        htmlSupport: {
          allow: [
            {
              styles: true,
              classes: true
            }
          ]
        }
      }
    })
    const value = computed(() => props.modelValue)
    const toolbarItems = computed(() => [
      'undo',
      'redo',
      '|',
      'findAndReplace',
      'selectAll',
      '|',
      'insertImage',
      'insertTable',
      'blockQuote',
      'mediaEmbed',
      'codeBlock',
      'pageBreak',
      'horizontalLine',
      'specialCharacters',
      'sourceEditing',
      '-',
      'heading',
      '|',
      'bold',
      'italic',
      'underline',
      'strikethrough',
      {
        label: 'Basic styles',
        icon: 'text',
        items: [
          'fontSize',
          'fontFamily',
          'fontColor',
          'fontBackgroundColor',
          'highlight',
          'superscript',
          'subscript',
          'code',
          '|',
          'textPartLanguage',
          '|'
        ]
      },
      'removeFormat',
      '|',
      'alignment',
      '|',
      'bulletedList',
      'numberedList',
      'todoList',
      '|',
      'outdent',
      'indent'
    ])

    return {
      textEditorOptions,
      value,
      toolbarItems
    }
  }
}
</script>
