<template>
  <div class="grid grid-cols-2">
    <div class="xl:col-span-1 lg:col-span-1 col-span-2">
      <Checkbox v-model="data.is_printer_access_show" :value="1" :value-off="0" class="font-medium mb-6" color="teal" view="smooth" type="toggle" block>Enable "Don't have a printer"</Checkbox>
    </div>
  </div>
</template>
<script>
import { computed, defineComponent } from 'vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'

export default defineComponent({
  components: {
    Checkbox
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        is_printer_access_show: 0
      })
    }
  },
  emits: ['change', 'data'],
  setup(props, { emit }) {
    const data = computed({
      set(newData){
        emit('data', newData)
        emit('change', newData)
      },
      get: () => props.data
    })
    const rules = {
      is_printer_access_show: {
        required
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    return {
      data,
      validation,
      validate
    }
  }
})
</script>