<template>
  <div>
    <Card class="mb-6">
      <template #header>
        <div class="flex items-center justify-between">
          <div>
            <h5 class="text-lg">Shipping Kit</h5>
          </div>
          <div>
            <Button
              @click="modal.template_list = true"
              :disabled="loading.fetch || loading.preview"
              color="teal"
              dense
              ><span class="mx-2">Templates</span></Button
            >
          </div>
        </div>
      </template>
      <template #default>
        <div>
          <Alert
            v-if="useLegacyTemplate"
            class="mb-6"
            color="orange"
            view="smooth"
          >
            <div class="flex items-center space-x-2 -mx-4 -my-4 p-2">
              <div>
                <i class="material-icons">error</i>
              </div>
              <div>You are currently using a legacy shipping kit template and not the one shown below. Please save the new template below to activate</div>
            </div>
          </Alert>
          <TextEditor v-model="shippingKit.textinfo">
            <template #toolbar>
              <div class="flex items-center ml-auto">
                <a
                  :class="[
                    'mx-6',
                    'link',
                    loading.preview || loading.fetch ? 'link--disabled' : ' '
                  ]"
                  href="#!"
                  @click.prevent="handleConfirmRestoreTemplate"
                  >Restore to Default</a
                >
                <Button
                  @click="handlePreviewShippingKit"
                  :loading="loading.preview || loading.fetch"
                  class="mx-2"
                  color="teal"
                  view="outlined"
                  dense
                >
                  <span class="mx-2"
                    ><i class="material-icons mr-1">visibility</i> Preview</span
                  >
                </Button>
                <Button
                  @click="handleUpdateShippingKit"
                  :loading="loading.update || loading.fetch"
                  :disabled="loading.fetch || loading.preview"
                  class="mx-2"
                  color="teal"
                  dense
                >
                  <span class="mx-2"
                    ><i class="material-icons mr-1">save</i> Save & Apply</span
                  >
                </Button>
              </div>
            </template>
          </TextEditor>
          <TableShortCodes :short-codes="shortCodes" />
        </div>
      </template>
    </Card>
    <ModalPreview v-model="modal.preview" :pdf="previewPdf">
      <template #actions>
        <div class="flex items-center justify-between">
          <div>
            <a
              @click.prevent="modal.preview = false"
              href="#!"
              class="text-red-04"
              >Cancel</a
            >
          </div>
          <div>
            <Button
              @click="handleDownloadPreviewedShippingKit"
              color="teal"
              dense
            >
              <span
                ><i class="material-icons">insert_drive_file</i> Download
                Shipping Kit</span
              >
            </Button>
          </div>
        </div>
      </template>
    </ModalPreview>
    <ModalTemplateList
      ref="refModalTemplateList"
      v-model="modal.template_list"
      :templates="templates"
      :loading="loading.template_list"
      @select-template="handleSelectDefaultTemplate"
    >
      <template #actions>
        <div class="flex items-center justify-between">
          <div>
            <a
              @click.prevent="modal.preview = false"
              href="#!"
              class="text-red-04"
              >Cancel</a
            >
          </div>
          <div>
            <Button
              @click="
                () => {
                  modal.template = true
                  modal.template_list = false
                }
              "
              color="teal"
              dense
            >
              <span
                ><i class="material-icons">insert_drive_file</i> Use This
                Template</span
              >
            </Button>
          </div>
        </div>
      </template>
    </ModalTemplateList>
    <Modal
      v-model="modal.restore"
      hide-header
      hide-footer
      size="xs"
      style="z-index: 1001"
    >
      <template #default>
        <div class="text-center">
          <img
            class="w-32 mx-auto my-10"
            src="@/assets/icon/attention-warning.svg"
            alt="Warning icon"
          />
          <p class="text-center my-10 text-grey-04">
            All changes will be restored to original template. <br />
            Are you sure to restore to default?
          </p>
          <div class="flex items-center justify-center my-10">
            <Button
              @click="handleRestoreShippingKit"
              :loading="loading.restore || loading.fetch"
              class="mx-2"
              color="orange"
              view="outlined"
              >Yes, Restore</Button
            >
            <Button @click="modal.restore = false" class="mx-2" color="teal"
              >Cancel</Button
            >
          </div>
        </div>
      </template>
    </Modal>
    <Modal
      v-model="modal.template"
      size="xs"
      hide-footer
      hide-header
      style="z-index: 1001"
    >
      <template #default>
        <div class="text-center">
          <img
            class="w-32 mx-auto my-10"
            src="@/assets/icon/attention-warning.svg"
            alt="Warning icon"
          />
          <p class="text-center my-10 text-grey-04">
            Using a template will reset the content of your shipping kit. Are
            you sure you want to change your shipping kit?
          </p>
          <div class="flex items-center justify-center my-10">
            <Button
              @click="handleUpdateDefaultTemplate"
              :loading="loading.update_default_template || loading.fetch"
              class="mx-2"
              color="orange"
              view="outlined"
              >Yes, Change</Button
            >
            <Button
              @click="
                () => {
                  modal.template = false
                  modal.template_list = true
                }
              "
              class="mx-2"
              color="teal"
              >Cancel</Button
            >
          </div>
        </div>
      </template>
    </Modal>
  </div>
</template>
<script>
import { defineComponent, ref } from 'vue'
import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'

import TextEditor from './ShippingKitTemplateTextEditor.vue'
import ModalPreview from './ShippingKitTemplateModalPreview.vue'
import ModalTemplateList from './ShippingKitTemplateModalTemplateList.vue'
import TableShortCodes from './ShippingKitTemplateTableShortCodes.vue'

export default defineComponent({
  components: {
    Card,
    Button,
    Modal,
    Alert,
    TextEditor,
    ModalPreview,
    ModalTemplateList,
    TableShortCodes
  },
  props: {
    shippingKit: {
      type: Object,
      default: () => ({
        id: null,
        key: null,
        name: null,
        textinfo: null
      })
    },
    loading: {
      type: Object,
      default: () => ({
        fetch: false,
        update: false,
        preview: false,
        restore: false,
        template_list: false,
        update_default_template: false
      })
    },
    modal: {
      type: Object,
      defaut: () => ({
        preview: false,
        restore: false,
        template: false,
        template_list: false
      })
    },
    previewPdf: {
      type: [String, ArrayBuffer],
      default: null
    },
    templates: {
      type: Array,
      default: () => []
    },
    shortCodes: {
      type: Array,
      default: () => []
    },
    useLegacyTemplate: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'preview',
    'update:shippingKit',
    'update',
    'restore',
    'download',
    'updateDefault'
  ],
  setup(props, { emit }) {
    const selectedDefaultTemplate = ref({
      id: null,
      key: null,
      name: null
    })
    const handlePreviewShippingKit = () => {
      emit('preview', true)
    }
    const handleUpdateShippingKit = () => {
      emit('update', true)
    }
    const handleRestoreShippingKit = () => {
      if (props.loading.restore) return
      emit('restore', true)
    }
    const handleDownloadPreviewedShippingKit = () => {
      emit('download', true)
    }
    const handleUpdateDefaultTemplate = () => {
      emit('updateDefault', selectedDefaultTemplate.value)
    }
    const handleSelectDefaultTemplate = (selectedTemplate = undefined) => {
      if (!selectedTemplate) return
      selectedDefaultTemplate.value = selectedTemplate
    }
    const handleConfirmRestoreTemplate = () => {
      if (props.loading.fetch || props.loading.preview) return
      props.modal.restore = true
    }
    return {
      handlePreviewShippingKit,
      handleUpdateShippingKit,
      handleRestoreShippingKit,
      handleDownloadPreviewedShippingKit,
      handleUpdateDefaultTemplate,
      handleSelectDefaultTemplate,
      handleConfirmRestoreTemplate
    }
  }
})
</script>
