<template>
  <Modal
    v-model="isVisible"
    title="Preview"
    size="lg"
    :hide-footer="!hasActionSlot"
    no-padding
    style="z-index: 1001"
  >
    <template #default>
      <div class="mb-6">
        <VuePdf
          style="height: 60vh"
          :pdf="pdf"
          :config="viewerConfig"
          theme="dark"
        />
      </div>
    </template>
    <template v-if="hasActionSlot" #footer>
      <slot name="actions"></slot>
    </template>
  </Modal>
</template>
<script>
import { computed, reactive, defineComponent } from 'vue'

import Modal from 'bbUi/components/modal/modal.vue'
import VuePdf from 'vue3-pdf-app'

export default defineComponent({
  components: {
    Modal,
    VuePdf
  },
  props: {
    modelValue: {
      type: [Boolean, String, Number],
      default: false
    },
    pdf: {
      type: [String, ArrayBuffer],
      default: null
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit, slots }) {
    const isVisible = computed({
      set: (value) => emit('update:modelValue', value),
      get: () => props.modelValue
    })
    const viewerConfig = reactive({
      toolbar: {
        toolbarViewerRight: false
      },
      secondaryToolbar: false
    })
    const hasActionSlot = computed(() => !!slots.actions)
    return {
      isVisible,
      viewerConfig,
      hasActionSlot
    }
  }
})
</script>
<style lang="scss">
.pdf-app.dark {
  --pdf-toolbar-color: #393939 !important;
  --pdf-app-background-color: #393939 !important;
  --pdf-overlay-container-dialog-color: #393939 !important;
  --pdf-sidebar-content-color: #393939 !important;
}
</style>
